.title-around-span {
    position: relative;
    text-align: center;
    color: #484848;
    font-size: 14px;

    &:before {
        content: "";
        display: block;
        height: 1px;
        width: 100%;
        position: absolute;
        left: 0;
        top: 50%;
        background-color: #E8E8E8;
    }

    span {
        position: relative;
        z-index: 1;
        padding: 0 15px;
        background: #ffff;
    }
}


